#cv {
  width: 15rem;
  height: 4rem;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  top: 3rem;
  left: 3rem;
}

h1 {
  margin: 0;
  font-size: 1.5rem;
}

h2 {
  margin: 0;
  font-size: 1rem;
}

#cv_lines {
  display: flex;
  align-items: center;
}

#cv_lines_thick {
  display: block;
  top: 0;
  left: 0;
  background-color: white;
  height: 0.2rem;
  width: 8rem;
}

#cv_lines_thin {
  display: block;
  top: 0;
  left: 0;
  background-color: lightgray;
  height: 0.05rem;
  width: 6rem;
}

@media (max-width: 1100px) {
  #cv {
    left: 50%;
    transform: translateX(-50%);
    width: 8rem;
    height: 2.5rem;
    top: 2rem;
  }
  h1 {
    font-size: 0.8rem;
  }
  h2,
  h3,
  p {
    font-size: 0.6rem;
  }
}
